import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import information from '@/pages/information'
import informationDetail from '@/pages/informationDetail'
import addPost from '@/pages/addPost'

import shopInfo from '@/pages/index/transfer/shopInfo'   //网店信息
import transferred from '@/pages/index/transfer/transferred'   //网店
import transferredDetail from '@/pages/index/transfer/transferredDetail'  //网店发布
import transferredOrder from '@/pages/index/transfer/order'  //网店订单
import evaluation from '@/pages/index/transfer/evaluation'  //网店估价

import bidding from '@/pages/index/bidding/bidding'  //竞价
import biddingAdd from '@/pages/index/bidding/biddingAdd'  //竞价发布
import biddingDetail from '@/pages/index/bidding/biddingDetail'  //竞价详情

import answer from '@/pages/index/answer/answer'  //回答
import answerAdd from '@/pages/index/answer/answerAdd'  //发布回答
import answerDetail from '@/pages/index/answer/answerDetail'  //回答详情

import User from '@/pages/user/user' //个人中心
import collection from '@/pages/user/collection' //我的收藏
import certification from '@/pages/user/certification' //实名认证
import release from '@/pages/user/release' //我的发布

Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/information',
      name: 'information',
      component: information
    },
    {
      path: '/post/add',
      name: 'addPost',
      component: addPost
    },
    {
      path: '/information/:id',
      name: 'informationDetail',
      component: informationDetail
    },
    {
      path: '/user/user',
      name: 'User',
      component: User
    },
    {
      path: '/user/collection',
      name: 'collection',
      component: collection
    },
    {
      path: '/user/certification',
      name: 'certification',
      component: certification
    },
    {
      path: '/user/release',
      name: 'release',
      component: release
    },
    {
      path: '/index/transfer/shopInfo',
      name: 'shopInfo',
      component: shopInfo
    },
    {
      path: '/index/transfer/transferred',
      name: 'transferred',
      component: transferred
    },
    {
      path: '/index/transfer/transferredDetail',
      name: 'transferredDetail',
      component: transferredDetail
    },
    {
      path: '/index/transfer/order',
      name: 'transferredOrder',
      component: transferredOrder
    },
    {
      path: '/index/transfer/evaluation',
      name: 'evaluation',
      component: evaluation
    },
    {
      path: '/index/bidding/bidding',
      name: 'bidding',
      component: bidding
    },
    {
      path: '/index/bidding/biddingAdd',
      name: 'biddingAdd',
      component: biddingAdd
    },
    {
      path: '/index/bidding/biddingDetail/:id',
      name: 'biddingDetail',
      component: biddingDetail
    },
    {
      path: '/index/answer/answer',
      name: 'answer',
      component: answer
    },
    {
      path: '/index/answer/answerAdd',
      name: 'answerAdd',
      component: answerAdd
    },
    {
      path: '/index/answer/answerDetail/:id',
      name: 'answerDetail',
      component: answerDetail
    }
    
  ]
})



