<template>
  <div class="contentHtml">
	<van-swipe :autoplay="3000">
	  <van-swipe-item v-for="img in images">
	  	<img class="width100" :src="img" />
	  </van-swipe-item>
	</van-swipe>
	<ul class="flex iconType">
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/transfer/transferredDetail')" src="../../../assets/weibo@3x.png"/>
			<p>出售网店</p>
		</li>
		<li>
			<img v-on:click="hrefTo('hrefTo','/index/transferredDetail')" src="../../../assets/jingbiaohuida@3x.png"/>
			<p>发布求购</p>
		</li>
		<li>
			<img v-on:click="hrefTo('hrefTo','href')" src="../../../assets/kechengguanli@3x.png"/>
			<p>网店评估</p>
		</li>
		<!--<li>
			<img v-on:click="hrefTo('hrefTo','href')" src="../../../assets/jingbiaohuida@3x.png"/>
			<p>已购网店</p>
		</li>-->
	</ul>
  	
  	<ul class="listBox">
  	  	<template v-for="listData in listDatas">
	  		<li v-on:click="hrefTo('href',listData.id)">
	    			<div class="leftBox">
	    				<div class="listTitle">{{listData.title.rendered}}</div>
	    				<div class="listInfo">
	    					<template v-for="tag in listData.tags">
	    						<span class="but1">{{tag}}</span>&emsp;
	    					</template>
	    					<span>新媒之家&emsp;{{listData.date | timeFormatter}}&emsp;阅读:{{listData.views}}</span>
	    				</div>
	    			</div>
	    			<div class="rightBox"><img v-bind:src="listData.post_pic"></div>
	    		</li>
    		</template>
     </ul>
     <div class="lodingDiv loding"></div>
  </div>
</template>

<script>
$("title").text("转让");
export default {
  name: 'contentHtml',
  data () {
    return {
    	images: [
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg',
        'https://img.yzcdn.cn/2.jpg'
    ],
    	listDatas:[],
    	input:'',
    	message:'',
    	this_page:1
    }
  },
  created:function(){
  	var _this = this;
  	//判断滑到底部
	$(document).scroll(function(){
	  var top = $(document).scrollTop() //距离顶部高度
	  var windowH = $(window).height(); //可视高度
	  var height = $(document).height();  //总高度
	  if(top+windowH==height){
	  	var addHtml = $(".lodingDiv").is(":visible");
	  	if(addHtml){
	  		return false;
	  	}else{
	  		_this.getList();
	  		return false;
	  	}
	  }
	})
	
	_this.getList();
  },
  methods: {
  	getList:function(){
  		var _this = this;
  		$(".lodingDiv").text('').addClass("loding").show();
    		var page = _this.this_page;
    		var per_page = 6;
	  	$.get('http://121.14.27.27:88/wp-json/wp/v2/posts',{"page":page,"per_page":per_page},function(res){
	  		$(".lodingDiv").hide();
	  		console.log(res)
	  		var start = _this.listDatas.length;
			for(var i = 0; i < res.length; i++) {
		      _this.listDatas[start+i] = res[i];
		    }
			_this.$forceUpdate();
		    _this.this_page = _this.this_page+1;
			if(res.length < per_page){
				$(".lodingDiv").text("没有更多了").removeClass("loding").show();
				var lengths = $(".listBox li").length;
			}
		})
  	},
	hrefTo(type,val){
		//ios跳转页面
		if(this.utilIsLocalhost()==false){
			if(type=="hrefTo"){
				var content = {"type":"hrefTo","href":val}
			}else{
				var content = {"type":"href","id":val}
			}
		    	var data = JSON.stringify(content);
		    ttf.nslog(data)
	  	}else{
	  		if(type=="hrefTo"){
	  			this.$router.push(val)
	  		}else{
	  			this.$router.push('/information/'+val)
	  		}
  		}
	}
  }
}

import Vue from 'vue'
Vue.filter('timeFormatter',function (value){
	return value.substring(5,10);
});
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.iconType{text-align: center;font-size: 1.3rem;background: #fff;margin-bottom:10px;}
	.iconType li{width: 25%;padding:15px;}
	.iconType li img{width:3rem;height: 3rem;}
</style>



