<script>
$("title").text("列表");
import {Toast} from 'vant';
export default {
  name: 'indexHtml',
  data () {
    return {
    	listDatas:'',
    	msg: '',
    	input:'',
    	message:''
    }
  },
  created:function(){
	if(this.utilIsLocalhost()==true){
//		this.register();
		this.login();
	}
  	
  },
  methods: {
    change: function (event) {
    		this.msg = $("p").text()
    },
    //分享
    share: function (type) {
    		var content = {"type":type,"title":"title","info":"info","img":"img","id":"id"}
        	var data = JSON.stringify(content);
        	console.log(data)
		ttf.nslog(data)
    },
    //注册
    register: function (type) {
    		var _self=this;
		var username = '15259';
		var password = '123456';
		var roles = 'author';//对应值为subscriber订阅者，contributor投稿者，author作者
		var data =  {
		        "username":"576038240", //用户名
		        "password":"123456",//密码
		        "email":"576038240@qq.com",//邮箱
		        "groupid":1,//会员组ID
		        "company":"公司名称不好写", //公司名称
		        "truename":"真实姓名干嘛告诉你",//真实姓名
		        "mycall":"15259641845"//联系电话
		    };
		
		$.post(_self.apiUrl+'/e/wxapi/register.php',data,function(data){
			if(data.status == false){
				Toast(data.content);
			}else{
				Toast(data.guiboweb);
			}
			console.log(data)
		},"json")
    },
    //登陆
    login: function (type) {
    		var _self=this;
		var data = {
	        "username":"576038240", //(必须)用户名
	        "password":"123456" //(必须)密码
	    }
		$.post(_self.apiUrl+'/e/wxapi/login.php',data,function(res){
			console.log(res)
			Toast(res.guiboweb);
			localStorage.setItem("user",JSON.stringify(res))
		},"json")
    }
  }
}

</script>

